// Study Resources Text
const StudyResource = [
    "<div><p>Când citim în Geneza, ca parte din blestemul sau condamnarea asupra mamei Eva şi indirect asupra tuturor fiicelor ei, cuvintele: „Dorinţa ta se va ţine după soţul tău şi el va stăpâni (Cornilescu -- n. e.) peste tine”, şi apoi vedem cum a fost exercitată această stăpânire pretutindeni în lume, găsim că în multe cazuri aceasta a fost o stăpânire tiranică, iar puterea minţii şi naturii bărbatului decăzut a fost adesea exercitată spre dauna soţiei şi copiilor în loc să fie spre mângâierea şi avantajul lor. Toţi bărbaţii şi toate femeile bune şi nobile trebuie să dezaprobe o astfel de stare de lucruri; şi nu putem presupune decât că un astfel de abuz de putere este respingător şi josnic în ochii Creatorului.</p>" +
    "<p>Abuzul de putere fizică şi mintală din partea unor soţi şi taţi a avut ca răspuns propria lor nefericire şi degradarea generală a omenirii; pentru că, deşi femeia este prin natură înclinată să privească spre un cap şi să caute şi să asculte de ceea ce ea recunoaşte a fi o autoritate dreaptă („dorinţa după soţul ei”), totuşi abuzurile de conducere şi exemplele rele date unul altuia au deviat în măsură considerabilă ceea ce a fost şi încă mai este substratul dispoziţiei naturale a femeii. Obligată de necesitate să se apere împotriva cerinţelor iraţionale ale tiraniei şi egoismului, rezultatul general a fost demoralizator pentru toată omenirea, aşa încât deşi ordinea naturală şi scripturală este foarte general recunoscută, nici bărbaţii nici femeile în general nu ştiu cum să se adapteze la starea prezentă dezordonată şi dezorganizată a afacerilor sociale.</p><p>Ca urmare, găsim adesea bărbaţi decăzuţi zbătându-se după stăpânire şi poziţie de cap pentru care sunt cu totul incompetenţi, cu scopul de a o folosi abuziv în interese egoiste, în acelaşi timp nerecunoscând şi neapreciind autoritatea şi responsabilitatea lor cuvenită ca protectori ai familiei. Vedem femei, de asemenea degradate şi egoiste, înclinate nu numai să se revolte împotriva unei conduceri iraţionale şi necuvenite, ci chiar să conteste orice propunere, să se tocmească şi să se certe în privinţa ei; şi deşi nu pretind că întreţin familia, totuşi încearcă, direct sau indirect, să uzurpe autoritatea capului căminului, să preia şi să deţină controlul pungii şi familiei. Oriunde predomină aceste condiţii, fiind contrare intenţiei şi aranjamentului divin, ele produc mai curând sau mai târziu roade mai mult sau mai puţin amare -- oricât de înţelepte sau necesare ar părea ele la vremea respectivă. Roadele paşnice ale dreptăţii pot fi aşteptate numai prin urmarea ordinii divine naturale. S-ar putea argumenta că în starea de lucruri actuală necazul de genul acesta este inevitabil; că bărbatul egoist va trece peste limitele ordinii şi intenţiei divine, iar femeia egoistă va face la fel; că, prin urmare, pacea, ordinea şi binecuvântarea intenţionate pentru omul perfect nu pot fi realizate niciodată de către copiii lui decăzuţi şi că singurul remediu care se întrevede pentru necazul actual al familiei umane, venit prin căderea lui Adam şi prin nesocotirea planului divin, este restabilirea. Cu aceasta noi suntem de acord şi ne alăturăm din inimă la rugăciunea Domnului: „Vie Împărăţia Ta, facă-se voia Ta precum în cer aşa şi pe pământ”.</p></div>",

  "<div><p>Acum nu analizăm posibilitatea de a face ordine în dezordinea din omenire, ci aranjamentul cuvenit şi calea în viaţă pentru Noua Creaţie în legătură cu casa, familia etc. -- precum şi datoriile unuia faţă de altul ca soţi şi soţii, părinţi şi copii. Am putea, destul de cuvenit, să considerăm acest subiect sub titlul datoriile şi obligaţiile bărbaţilor şi femeilor creştine, dacă termenul creştin nu şi-ar fi pierdut mult din sensul lui iniţial, încât acum se înţelege că se referă la orice persoană care nu este nici evreu nici păgân. Strict vorbind, cuvântul creştin, care desemnează pe unul care crede în Isus din Nazaret şi-L urmează, este aplicabil numai la Noua Creaţie. Din cauză că abaterea este atât de obişnuită, atât de generală, noi ţinem atât de mult să-i distingem pe credincioşii cu adevărat consacraţi ca fiind Noua Creaţie.</p><p>Apostolul arată clar că legătura de căsătorie printre oameni este intenţionată sub aranjamentul divin să fie o imagine sau o ilustraţie a legăturii între Cristos şi Biserică, Mireasa Sa -- corpul Său. Limbajul este foarte explicit:</p><p>„Soţiilor, fiţi supuse soţilor voştri, ca Domnului, căci soţul este capul soţiei, după cum şi Hristos este capul Bisericii, El, Mântuitorul trupului. Şi după cum Biserica este supusă lui Hristos, tot aşa şi soţiile să fie supuse soţilor lor în toate. Soţilor, iubiţi-vă soţiile, după cum şi Hristos a iubit Biserica şi S-a dat pe Sine pentru ea, ca s-o sfinţească, curăţind-o prin spălarea cu apă prin Cuvânt, ca să înfăţişeze înaintea Sa această Biserică slăvită, fără pată, fără încreţitură sau altceva de felul acesta, ci sfântă şi fără defect. Tot aşa sunt datori şi bărbaţii să-şi iubească soţiile, ca pe trupurile lor. Cine-şi iubeşte soţia se iubeşte pe sine însuşi. Căci nimeni nu şi-a urât vreodată trupul său, ci-l hrăneşte şi-l îngrijeşte cu drag, ca şi Hristos Biserica; pentru că noi suntem mădulare ale trupului Său. De aceea va lăsa omul pe tatăl său şi pe mama sa şi se va lipi de soţia sa şi cei doi vor fi un singur trup. Taina aceasta este mare; dar vorbesc despre Hristos şi despre Biserică. Încolo, fiecare din voi să-şi iubească soţia ca pe sine şi soţia să se teamă de soţ”. Efes. 5:22-33.</p>" +
  "<p>Faptul că unirile tipice sunt în general atât de imperfecte şi nesatisfăcătoare nu anulează ideea că s-a intenţionat ca mariajul să fie un tip, întocmai cum şi multe din sacrificiile israeliţilor au fost imperfecte şi nesatisfăcătoare, dar cu toate acestea constituie tipuri ale sacrificiului adevărat. Noua Creaţie trebuie să aprecieze cu mult mai mult căsătoria tipică, pământească şi datoriile şi responsabilităţile ei cuvenite, datorită aprecierii ei faţă de unirea antitipică între Cristos şi Biserica Sa. Considerat astfel, fiecare bărbat creştin găseşte cea mai mare exemplificare posibilă a datoriilor şi responsabilităţilor sale faţă de soţia sa în grija Domnului faţă de Biserică şi faţă de fiecare interes al ei, pământesc, spiritual, prezent şi viitor, până la sacrificarea vieţii Sale pentru ea. La fel soţia, apreciind datoriile şi responsabilităţile Bisericii faţă de Domnul, discerne un ideal mai înalt al datoriei şi relaţiei soţiei cu soţul ei, ca ajutor al lui. Dar nu trebuie să aşteptăm ca aceste relaţii deosebite şi aplicarea lor cuvenită să poată fi discernute clar decât de către cei care au mintea lui Cristos. Ca atare, deşi îndemnăm pe toţi care contractează legătura căsătoriei să realizeze cât pot de mult idealul divin, notăm totuşi că nimeni nu poate înţelege, aprecia şi aplica toate principiile şi idealurile legate de acest tip, decât cei care au fost concepuţi de Spirit -- Creaţia Nouă -- fiindcă numai aceştia au mintea lui Cristos.</p>" +
  "<p>Se poate spune că indivizii din rasa umană nefiind decăzuţi toţi în acelaşi grad, adeseori se întâmplă ca soţia să posede calităţi ale minţii şi inimii superioare celor ale soţului ei. Atunci se ridică întrebarea, n-ar trebui ca în asemenea împrejurări o astfel de soţie, înzestrată cu talente, judecată şi capacităţi superioare, să fie privită ca şi cap al familiei, iar soţul ca şi ajutor? Noi răspundem, nu. Ordinea divină ar fi desconsiderată în astfel de căsătorie, deoarece nici o femeie n-ar trebui să ia în căsătorie un bărbat mai prejos decât ea în caracter şi talente -- unul spre care ea n-ar putea privi în mod cuvenit ca spre „capul” ei. Şi nici un bărbat n-ar trebui să ia în căsătorie pe o femeie superioară lui. N-ar trebui nici ca unul care a devenit Nouă Creatură în Cristos Isus să se înjuge la un jug nepotrivit cu unul care este încă din pământ, pământesc -- nu contează cât de nobilă şi onorabilă ar putea fi persoana. Că Noua Creatură trebuie să se căsătorească „numai în Domnul” este un sfat care n-ar trebui ignorat, şi nesocotirea lui a adus dificultăţi grave multora din poporul Domnului.</p></div>",

  "<div><p style='text-align: center'>Căsătoria Noilor Creaturi</p><p>Totuşi, odată ce legătura de căsătorie s-a încheiat, este prea târziu pentru regrete, şi pentru copilul lui Dumnezeu nu rămâne decât să-şi ducă la îndeplinire legământul de căsătorie fără rezerve, în literă şi spirit, în măsura capacităţii lui sau a ei. Dacă amândoi sunt Creaturi Noi şi căsătoria este potrivită, n-ar trebui să fie nici o dificultate pentru nici una din părţi în a decide asupra aranjamentelor şi reglementărilor cuvenite ale casei: totuşi, trebuie să fie luată cu grijă în considerare şi compatibilitatea dispoziţiei naturale şi a gusturilor. Adevăratul soţ creştin, având mintea lui Cristos, îşi va iubi soţia, îşi va aminti că s-a angajat prin legământ s-o iubească, să aibă grijă de ea, s-o aprovizioneze nu numai în privinţa necesităţilor fizice, ci şi a hrănirii inimii şi a sentimentelor. Un astfel de soţ nu va simţi că s-a achitat de datorie prin asigurarea ei doar cu necesităţile şi confortul de hrană, îmbrăcăminte şi adăpost, ci îşi va da seama că are obligaţia faţă de soţie să ia în considerare şi interesele ei mintale, morale şi spirituale. El nu va fi satisfăcut ca timpul ei să fie absorbit cu totul de datoriile şi grijile familiei, ci, în măsura capacităţii, va căuta şi cultivarea minţii ei, a inimii ei -- folosindu-şi calitatea de cap al familiei ca să-i aranjeze în aşa fel afacerile încât ea să aibă timp rezonabil pentru părtăşie spirituală şi studiere a Adevărului. Astfel de soţ nu va uita că în carnea sa muritoare, ca şi în a restului familiei umane, egoismul este mai mult sau mai puţin înrădăcinat şi ca Nouă Creatură el va fi atent ca această dispoziţie să nu le producă greutăţi sau daune altora -- în special soţiei şi copiilor săi, care sunt carne din carnea sa şi os din oasele sale.</p>" +
  "<p>Poziţia de cap în familie, astfel exercitată în promovarea binelui celor care sunt în grija sa pentru îndrumare, sfătuire etc., precum şi pentru asigurarea cu lucrurile necesare, va fi departe de a fi tiranie. Spiritul iubirii într-un astfel de soţ nu va ignora nici ceea ce-i place şi ceea ce nu-i place soţiei sale şi nici sfatul dat de ea în modul cuvenit. El va recunoaşte că în timp ce Adam cel perfect a posedat toate însuşirile bărbăţiei, separarea Evei a implicat şi separarea unora din aceste însuşiri: el va recunoaşte de asemenea că deşi puterea minţii şi corpului rămâne prin aranjament divin la bărbat şi-l face cap al familiei, cu toate acestea, există însuşiri posedate în mod special de femeie. Smerenia, care aparţine spiritului iubirii, îl va împiedica să fie orb faţă de însuşirile vrednice de stimă pe care Creatorul le-a destinat femeii şi va recunoaşte că însuşirile inimii şi minţii sale trebuie să fie suplimentate de celelate însuşiri care prin natură există îndeosebi la femeie. Ca atare, în măsura în care are „spiritul chibzuinţei\", va dori ajutorul soţiei sale, cooperarea ei, opiniile ei, compătimirea ei, iubirea ei, şi le va aprecia mult.</p><p>Aceasta însă nu înseamnă că a căuta sfatul soţiei înseamnă ascultare de opiniile ei în toate cazurile: rămâne pentru soţ să cântărească, să analizeze, să cumpănească, să hotărască -- dând sentimentelor soţiei sale o interpretare cuvenită, raţională, binevoitoare. Responsabilitatea de cap este asupra soţului şi el nu trebuie s-o evite. Ea este, prin obligaţie divină, parte din administrarea lui, pentru care va fi chemat în cele din urmă să dea socoteală.</p>" +
  "<p>La fel soţia, care este Creatură Nouă şi care s-a căsătorit „în Domnul\", şi care, după ce a exercitat puterea de judecată cuvenită, a făcut o căsătorie bună, n-ar prea trebui să aibă dificultate în a recunoaşte datoriile, responsabilităţile şi privilegiile poziţiei ei după trup. „Soţia să se teamă de soţ\", spune apostolul. Ea nu trebuie să aştepte de la alţii din afară să-i atragă atenţia că este deficitară în privinţa respectului ei ca soţie faţă de soţ, nici să aştepte ca soţul să-i indice faptul că el crede că nu-l tratează cu respectul cuvenit lui conform legământului de căsătorie şi conform descrierii scripturale a datoriilor unei soţii. Dimpotrivă, privind în jurul ei să vadă care-i sunt responsabilităţile şi datoriile de soţie, să caute să-şi respecte soţul şi să-şi dea seama că sensul angajamentului său de căsătorie conform Scripturilor înseamnă tocmai acest lucru -- indiferent ce ar putea însemna acesta conform lumii şi diferitelor concepţii omeneşti. Respectul faţă de soţ înseamnă mult şi intră de fapt în toate afacerile vieţii, atingând şi influenţând fiecare faptă, cuvânt şi gând în privinţa casei şi intereselor ei.</p></div>",

  // "<div><p>Apostolul Petru atrage atenţia asupra acestei probleme într-un limbaj cumva similar, zicând: „Soţiilor, fiţi supuse şi voi soţilor voştri . . . în frumuseţea nepieritoare a unui duh blând şi liniştit, care este de mare preţ înaintea lui Dumnezeu. Căci aşa se împodobeau odinioară sfintele femei care nădăjduiau în Dumnezeu, fiind supuse soţilor lor, ca Sara, care asculta pe Avraam şi-l numea domnul ei\" (1 Pet. 3:1-6). După cum bărbatul care-şi cinsteşte soţia se cinsteşte pe el însuşi, tot aşa femeia care-şi respectă soţul se onorează pe ea însăşi. Dar acest respect faţă de soţ, ca domn, stăpân sau cap al casei, nu înseamnă robie, deoarece Biserica nu ocupă o poziţie slugarnică faţă de Domnul, nici n-are o frică slugarnică, ci un respect din iubire, din devotament -- şi acesta este exemplul.</p></div>",
  // "<div><p>Acest respect faţă de soţ nu înseamnă că soţia nu trebuie să-şi folosească judecata şi nu poate aduce la cunoştinţa soţului încercări sau greutăţi sau poveri prea grele pentru ea etc., dar prezentarea opiniilor, speranţelor şi dorinţelor ei nu trebuie să fie sub formă poruncitoare, ci în mod respectuos, care să-i recunoască poziţia de cap a soţului şi să caute să fie fericită şi mulţumită cu deciziile lui după ce i-a prezentat gândurile asupra unor subiecte de interes reciproc. Ea trebuie să caute să fie atât de plină de consideraţie, atât de înţeleaptă în administrarea afacerilor casei pe care i le-ar încredinţa soţul, încât să-i câştige tot mai mult încrederea şi să fie tot mai capabilă să îndeplinească datoriile importante ale unui ajutor în casă, fie că ele sunt mici, fie că sunt mari. Gândul că este un ajutor pentru soţ şi dorinţa de aprobare din partea lui se va vedea că este în armonie strictă cu sugestia apostolului în privinţa atitudinii cuvenite a Bisericii faţă de Domnul, în credincioşie şi în dorinţa de a-I avea aprobarea. Dar după cum pentru Biserică ar fi o nelegiuire să ignore Capul, pe Domnul, în oricare măsură în legătură cu lucrarea şi cu interesele ei, tot aşa soţia ar trebui să simtă că ar fi o nelegiuire şi o încălcare a legământului ei dacă ar încerca să reglementeze casa pământească şi în vreo măsură să-l ignore pe acela pe care s-a angajat să-l respecte ca şi cap al familiei.</p></div>",
  // "<div><p>În cazul Noilor Creaturi care nu formează o pereche potrivită -- unde soţia este în mod evident superioară -- există pericolul dificultăţii în aranjarea afacerilor. Dacă soţia are judecată mai bună în privinţa conducerii casei, în privinţa cheltuirii banilor, în privinţa educării copiilor etc., ea nu are ca atare libertatea să-şi asume poziţia de cap al familiei, să-i poruncească soţului şi să-l îndrume ca şi cum el ar fi unul dintre copiii ei sau un servitor. Astfel de încălcare a aranjamentului divin va produce desigur dezavantaj spiritual, dacă nu financiar şi material, nu numai pentru bărbat, ci şi pentru femeie.</p><p>În asemenea condiţii bărbatul şi-ar pierde treptat puţina bărbăţie pe care o posedă, ar ceda treptat totul în mâna soţiei şi ar deveni numai unealta ei, robul ei, care să se îngrijească de trai şi să-i împlinească poruncile. Astfel de stare n-ar fi avantajoasă pentru soţ ca Nouă Creatură; astfel de degradare a cărnii lui ar reacţiona desigur nefavorabil asupra lui, l-ar despiritualiza şi i-ar împiedica creşterea în har, în cunoştinţă şi în serviciul Adevărului. Şi asupra soţiei efectul s-ar dovedi dăunător în măsura în care s-ar urma calea greşită -- mult sau puţin. Dacă ar fi un caz extrem -- când soţul ar lăsa responsabilităţile lui să cadă asupra ei, sau când ea le-ar prelua treptat din mâinile lui -- soţia ar simţi greutatea acestora pe lângă datoriile ei de mamă; şi în încercarea ei de a fi atât soţ cât şi soţie, atât tată cât şi mamă, în mod sigur ea ar deveni mai mult sau mai puţin „femeie de afaceri\", ar fi mai mult sau mai puţin încrezută şi conştientă de sine. Prietenii ei ar putea să-i admire tăria de caracter pe care o manifestă şi ar putea să considere că direcţia în care merge este inevitabilă -- ar putea chiar s-o încurajeze şi s-o susţină ca pe un exemplu lăudabil de femeie hotărâtă, dar nici unul din ei n-ar iubi-o cum ar fi iubit-o dacă s-ar fi dezvoltat pe linia adevăratei femei şi adevăratei soţii. Mai mult, însuşirile cărnii cultivate printr-un astfel de curs ar reacţiona nefavorabil asupra ei ca Nouă Creatură în Cristos, şi, fără să-şi dea seama, ar deveni mai puţin spirituală şi mai conştientă de sine în lucruri care aparţin de Biserică.</p></div>",
  // "<div><p>Calea cuvenită de urmat în asemenea caz de căsătorie nepotrivită între Creaturile Noi este ca soţul să-şi spună: Mi-am luat o soţie contra aranjamentului divin. Mi-am expus astfel fericirea casei unui mare risc. Singura mea cale este acum să mă străduiesc după cele mai bune capacităţi pe care le am să mă ridic la idealul meu cel mai înalt de soţ adevărat -- să mă modelez cât pot de mult după exemplul Domnului. Voi avea nevoie să fiu cu atât mai atent, cu fiecare cuvânt şi faptă a mea -- să caut cu atât mai serios înţelepciunea care vine de sus, ca să pot îndeplini mai bine datoriile de cap al acestei case, pentru care îmi dau seama că nu sunt calificat din punct de vedere natural.</p></div>",
];

export default StudyResource;
